body {
  font-family: $main-font-family; }

.container {
  max-width: $container-xl;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto; }

.link {
  color: $mainColor; }

.header {
  color: $textColor;
  font-size: 42px;
  line-height: 50px; }

.text {
  color: $textColor;
  font-family: $font-family;
  font-weight: 300;
  font-size: 20px;
  line-height: 35px;
  opacity: 0.8;
  margin-bottom: 1em; }

.button {
  background-color: $mainColor;
  color: $lightColor;
  font-family: $font-family;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  padding: 18px 57px 19px 57px;
  border-radius: 50px;
  transition: .3s;

  &:hover, &:focus {
    background-color: $bg-hover; }

  &:active, &:focus-visible {
    background-color: $bg-active; } }

.input-block {
  position: relative; }

.label {
  color: $textColorTransparent;
  opacity: 0;
  position: absolute;
  left: 27px;
  top: -8px;
  font-family: $font-family;
  font-size: 12px;
  line-height: 16px;
  background-color: $lightColor;
  padding: 0 7px;
  border-radius: 3px;

  &:active {
    display: inline-block; } }


.input {
  border: 2px solid $mainColor;
  vertical-align: middle;
  padding: 12px 27px 12px 27px;
  font-family: $font-family;
  font-size: 16px;
  line-height: 25px;
  border-radius: 32px;
  width: 100%;

  &::placeholder {
    color: $textColorTransparent; }

  &:not(:placeholder-shown), &:focus {
    border: 2px solid $textColorTransparent;

    & ~ .label {
      opacity: 1; } }

  &:invalid {
    box-shadow: none; }

  &:-moz-submit-invalid {
    box-shadow: none; }

  &:-moz-ui-invalid {
    box-shadow: none; }

  &:invalid:focus:placeholder-shown, &:invalid:focus:not(:placeholder-shown), &:invalid:not(:focus):not(:placeholder-shown) {
    border: 2px solid $errorColor;

    & ~ .label {
      color: $errorColor;
      opacity: 1; } } }

.divider {
  height: 1px;
  width: 100%;
  background-color: $borderColor2; }

// Font icons:
.fico {
  display: inline-block;

  &::before {
    display: block; } }

@media screen and (max-width: $lg) {
  .container {
    max-width: none;
    padding: 0; } }
