// $xs: 575.9px
@media screen and (max-width: $xs) {
  .main-produce {
    padding-right: 15px;
    padding-left: 15px;

    &__description {
      margin-right: 0;
    }

    &__description-title {
      font-size: 35px;
      line-height: 40px;
    }

    &__description-text {
      font-size: 18px;
      line-height: 30px;
    }

    &__item {
      margin-right: 0;
      width: 100%;
    }

    &__item-text {
      font-size: 18px;
      line-height: 30px;
    }
  }
}

@keyframes item-anim {
  0% {
    background-color: $lightColor;
  }
  48% {
    background-color: $lightColor;
  }
  50% {
    background-color: $mainHover;
  }
  100% {
    background-color: $mainHover;
  }
}

@keyframes text-animation {
  0% {
    opacity: .8;
    color: $textColor;
  }
  48% {
    opacity: .8;
    color: $textColor;
  }
  50% {
    color: $lightColor;
    opacity: 1;
  }
  100% {
    color: $lightColor;
    opacity: 1;
  }
}

@keyframes fill-anim {
  0% {
    fill: $mainHover;
  }
  48% {
    fill: $mainHover;
  }
  50% {
    fill: $lightColor;
  }
  100% {
    fill: $lightColor;
  }
}

@keyframes stroke-anim {
  0% {
    stroke: $mainHover;
  }
  48% {
    stroke: $mainHover;
  }
  50% {
    stroke: $lightColor;
  }
  100% {
    stroke: $lightColor;
  }
}

